import styled from 'styled-components';
import { auth, provider } from '../firebase';
import config from './../config';
import {useDispatch, useSelector} from 'react-redux';
import {useHistory} from 'react-router-dom';
import {setUserLoginDetails ,selectUserName,selectUserPhoto, setSignOutState} from '../features/user/userSlice'
import { useEffect } from 'react';

const Header = (props) => {

    // dispatch is used to store the value in redux and selector to retrieve data
    const dispatch = useDispatch();
    const history = useHistory();
    const username = useSelector(selectUserName);
    const userPhoto = useSelector(selectUserPhoto);

    useEffect(()=>{
        auth.onAuthStateChanged(async (user) => {
            if(user){
                setUser(user);
                history.push(config.baseURl +'/home')
            }
        })
    }, [username])

    const handleAuth = () => {
      if (!username) {
        auth
          .signInWithPopup(provider)
          .then((result) => {
            setUser(result.user);
          })
          .catch((error) => {
            alert(error.message);
          });
      }else if(username) {
          auth
            .signOut()
            .then(() => {
              dispatch(setSignOutState());
              history.push(config.baseURl + "/");
            })
            .catch((error) => {
              alert(error.message);
            });
      }
    };

    const setUser = (user) => {
        dispatch(setUserLoginDetails({
            name: user.displayName,
            email: user.email,
            photo: user.photoURL,
        }))
    };

    return(
        <Nav>
            <Logo>
                <img src={config.baseURl +"/images/logo.svg"} alt="Disney+" />
            </Logo>
            {
                !username ?
                <Login onClick={handleAuth} >Login</Login>
                :
                <>
                <NavMenu>
                <a href={config.baseURl + "/home"}>
                    <img src={config.baseURl +"/images/home-icon.svg"} alt="home"/>
                    <span>HOME</span>
                </a>
                <a href='none'>
                    <img src={config.baseURl +"/images/search-icon.svg"} alt="SEARCH" />
                    <span>SEARCH</span>
                </a>
                <a href='none'>
                  <img src={config.baseURl +"/images/watchlist-icon.svg"} alt="WATCHLIST" />
                  <span>WATCHLIST</span>
                </a>
                <a href='none'>
                  <img src={config.baseURl +"/images/original-icon.svg"} alt="ORIGINALS" />
                  <span>ORIGINALS</span>
                </a>
                <a href='none'>
                  <img src={config.baseURl +"/images/movie-icon.svg"} alt="MOVIES" />
                  <span>MOVIES</span>
                </a>
                <a href='none'>
                  <img src={config.baseURl +"/images/series-icon.svg"} alt="SERIES" />
                  <span>SERIES</span>
                </a>
            </NavMenu>
            <SignOut>
                <UserImage src={userPhoto} alt={username}/>
                <DropDown>
                    <span onClick={handleAuth}>Sign out</span>
                </DropDown>
            </SignOut>
            </>
        }
        </Nav>
    )
};

const Nav = styled.nav`
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    height: 70px;
    background-color: #090b13;
    display: flex;
    justify-content: space-between;
    padding: 0 36px;
    align-items: center; 
    letter-spacing: 16px;
    z-index: 3;
`;

const Logo = styled.a`
    padding: 0;
    width: 80px;
    margin-top: 4px;
    max-height: 70px;
    font-size: 0;
    display: inline-block;

    img{
        display: block;
        width: 100%;
    }
`;

const NavMenu = styled.div`
    display: flex;
    flex-flow: row nowrap;
    align-items: center;
    justify-content: flex-end;
    height: 100%;
    margin: 0px;
    padding: 0px;
    position: relative;
    margin-right: auto;
    margin-left: 25px;
    
    a {
        display: flex;
        align-items: center;
        padding: 0 12px;
        
        img {
            height: 20px;
            min-width: 20px;
            width: 20px; 
            z-index: auto;
        }

        span {
            color: rgb(249,249,249);
            font-size: 13px;
            letter-spacing: 1.42px;
            line-height: 1.08;
            white-space: nowrap;
            position: relative;
            padding: 2px 0px; 
            
            &:before {
                background-color: rgb(249,249,249);
                border-radius: 0px 0px 4px 4px;
                bottom: -6px;
                content: "";
                height: 2px;
                left: 0px;
                right: 0px;
                opacity: 0;
                position: absolute;
                transform-origin: left center;
                transform: scaleX(0);
                transition: all 250ms cubic-bezier(0.25, 0.46,0.45, 0.94) 0s;
                visibility: hidden;
                width: auto;
            }
            
        }

         &:hover{
             span:before {
                 transform: scaleX(1);
                 visibility: visible;
                 opacity: 1 !important;
             }
         }

    }

    

    @media (max-width: 768px) {
        display: none;
    }
`;

const Login = styled.a`
    background-color: rgba(0,0,0,0.6);
    text-transform: uppercase;
    padding: 8px 16px;
    letter-spacing: 1.5px;
    border: 1px solid #f9f9f9;
    border-radius: 4px;
    transition: all .2s ease 0s;

    &:hover {
        background-color: #f9f9f9;
        color: black;
        border: transparent;
    }
`;

const UserImage = styled.img`
    height: 90%;
    border: 1px solid white;
    border-radius: 50%;
`;

const DropDown = styled.div`
    position: absolute;
    top: 48px;
    cursor: pointer;
    right: 0px;
    background: rgb(19,19,19);
    border: 1px solid rgb(151,151,151,0.34);
    border-radius: 4px;
    box-shadow: rgb(0 0 0/ 50%) 0px 0px 18px 0px;
    padding: 10px;
    font-size: 14px;
    letter-spacing: 3px;
    width: 100px;
    opacity: 0;
`;

const SignOut = styled.div`
    position: relative;
    height: 48px;
    width: 48px;
    display: flex;
    cursor: pointer;
    align-items: center;
    justify-content: center;

    &:hover{
        ${DropDown} {
            opacity: 1;
            transition-duration: 1s;
        }
    }
`;

export default Header;